@import url(https://fonts.googleapis.com/css?family=Lato);
@import url(https://cdnjs.cloudflare.com/ajax/libs/font-awesome/4.6.3/css/font-awesome.css);
body {
    font-family: 'Orbitron', sans-serif;
    font-size: 14px;
}


/* each div takes up the entire page */

.home,
.contact {
    /* Height and width fallback for older browsers. */
    height: 100%;
    width: 100%;
    /* Set the height to match that of the viewport. */
    height: 100vh;
    /* Set the width to match that of the viewport. */
    width: 100vw;
    /* Remove any browser-default margins. */
    margin: 0;
}
.ui.text.container {
    max-width: 1200px;
}

.ui.secondary.inverted.menu {
    background-color: black;
    /* opacity: .7; */
}

.ui.secondary.inverted.menu a.item {
    color: white !important;
}

.ui.secondary.inverted.menu a.item:hover {
    color: #b8b8b8 !important;
}

.bgpm {
    /* background-size: cover; */
    color: rgba(255, 255, 255, .9);
    max-width: 100%;
    background: url(/kp-react-portfolio/static/media/pacmanbgburned.8cb7820b.png) no-repeat center center fixed;
    background-size: cover;
}


/* Image border */

.imageStyling {
    border: 5px solid #dddbd9;
    margin-bottom: .5em;
}


/* Minimum height of image */

.ui.image img,
.ui.image svg {
    min-height: 300px;
}


/* min-height needs to be changed back to auto on smaller screens */

.height {
    min-height: 15em;
}

.Typist {
    font-size: 2em;
}

.sourcecodeBtn {
    padding: 1em;
}
