a {
    text-decoration: none;
    color: #fff;
}

p>a:hover {
    color: #d9d9d9;
    text-decoration: underline;
}

h1,
h2,
h3,
h4,
h5,
h6 {
    margin: 1% 0 1% 0;
}

._12 {
    font-size: 1.2em;
}

._14 {
    font-size: 1.4em;
}

ul {
    padding: 0;
    list-style: none;
}

.footer-social-icons {
    width: 350px;
    display: block;
    margin: 0 auto;
}

.social-icon {
    color: #fff;
}

ul.social-icons {
    margin-top: 10px;
}

.social-icons li {
    vertical-align: top;
    display: inline;
    height: 100px;
}

.social-icons a {
    color: #fff;
    text-decoration: none;
}

.fa-facebook {
    padding: 10px 14px;
    transition: .5s;
    background-color: #322f30;
}

.fa-facebook:hover {
    background-color: #3d5b99;
}

.fa-twitter {
    padding: 10px 12px;
    transition: .5s;
    background-color: #322f30;
}

.fa-twitter:hover {
    background-color: #00aced;
}

.fa-rss {
    padding: 10px 14px;
    transition: .5s;
    background-color: #322f30;
}

.fa-rss:hover {
    background-color: #eb8231;
}

.fa-youtube {
    padding: 10px 14px;
    transition: .5s;
    background-color: #322f30;
}

.fa-youtube:hover {
    background-color: #e64a41;
}

.fa-linkedin {
    padding: 10px 14px;
    transition: .5s;
    background-color: #322f30;
}

.fa-linkedin:hover {
    background-color: #0073a4;
}

.fa-envelope {
    padding: 10px 9px;
    transition: .5s;
    background-color: #322f30;
}

.fa-envelope:hover {
    background-color: #e25714;
}
.projects {
    text-align: center;
    margin: 0 auto;
    min-height: 95%;
    width: 95%;
    /* position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0; */
}

.redbg {
    background-color: red;
}

.header {
    color: white;
}

.ui.card,
.ui.cards>.card {
    width: 100%;
}

.grid {
    position: relative;
    top: 15%;
}

.ui.card>.image>img,
.ui.cards>.card>.image>img {
    display: block;
    width: 100%;
    height: 100%;
    border-radius: inherit;
}

.icons {
    background-color: white;
}

.padding {
    padding: .5em;
    text-align: center;
}

/* Overlay Effect on Tech Stack Icons */

.overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0);
    transition: background 0.5s ease;
}

.overlayEffect:hover .overlay {
    /* display: block; */
    background: rgba(0, 0, 0, .3);
}

.title {
    position: absolute;
    width: 90%;
    top: 25%;
    font-weight: 700;
    font-size: 1.25em;
    text-align: center;
    text-transform: uppercase;
    color: white;
    z-index: 1;
    transition: top .5s ease;
    opacity: 0;
}

.overlayEffect:hover .title {
    opacity: 1;
}

.overlayEffect:hover .button {
    opacity: 1;
}


/* End of Overlay Effect on Tech Icons */
